<template>
  <div class="container">
    <div class="row">
      <h1 class="title">{{name}}'s To-Do List</h1>
    </div>
    <div class="row">
      <input v-model="curInput" />
      <button class="btn right" v-on:click="addNewTodo">Add</button>
      <div class="center right">

      <label class="text right">Hide</label>
      <input type="checkbox" class="check right" v-model="hide" />
      </div>
    </div>
    <div class="row">
      <div class="task-container">
        <div class="task-row" v-for="l in hiddenList" v-bind:key="l.list">
          <div class="task-title">{{l.task}}</div>
          <div class="task-check">
            <input type="checkbox" class="check" v-model="l.comp" />
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <button class="btn" v-on:click="deleteItem">Delete</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ToDoContainer",
  props: {
    name: String
  },
  data() {
    return {
      list: [],
      hide: true,
      curInput: ""
    };
  },
  methods: {
    addNewTodo() {
      if (this.curInput != "") {
        this.list.push({
          task: this.curInput,
          comp: false
        });
        localStorage.setItem("listItems", JSON.stringify(this.list));
        this.curInput = "";
      }
    },
    deleteItem() {
      this.list = this.list.filter(l => !l.comp);
      localStorage.setItem("listItems", JSON.stringify(this.list));
    }
  },
  created() {
    let data = localStorage.getItem("listItems");
    if (data != null) {
      this.list = JSON.parse(data);
    }
  },
  computed: {
    hiddenList() {
      return this.hide ? this.list.filter(l => !l.comp) : this.list;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title {
  font-weight: 800;
  margin: 0;
}
.text{
    margin-right:15%;
}
.center{
margin-top: 1%;
display:flex;
margin-right:5%;
}

input {
    margin-left:5%;
    border-radius:25px;
    border:none;
    height:15px;
    padding:1%;
}
.container {
  display: flex;
  flex-direction: column;
  margin: 10% 15%;
  width: stretch;
  color: white;
  padding: 2% 5%;
  border-radius: 25px;
  background-color: rgb(68, 68, 68);
}
.right {
    float:right;
}
.row {
  height: min-content;
  padding: 1% 0px;
}
.task-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.task-row {
  padding: 0.5% 2%;
}
.task-title {
  float: left;
}
.task-check {
  float: right;
}

.btn {
  padding: 1% 2%;
  border-radius: 5px;
  background-color: rgb(0, 89, 255);
  border: none;
  color: white;
  font-weight: bold;
}
.btn:focus {
  border: lightskyblue 5px;
}
</style>
